*,
*::before,
*::after {
  box-sizing: inherit; }


html {
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  font-size: 16px; }


body {
  color: $font-color;
  background-color: $background-color;
  line-height: $font-line-height; }

p, h1,h2,h3,h4,h5,h6 {
  margin-top: 0;
  margin-bottom: 24px; }

h1 {
  font-size: 3.375rem;
  line-height: 1.1; }

h2 {
  font-size: 2.25rem;
  line-height: 1.2; }

h3 {
  font-size: 1.5rem;
  line-height: 1.2; }

h4 {
  font-size: 1rem;
  line-height: 1.1; }

h5 {
  font-size: 0.667rem;
  line-height: 1.5; }


p {
  font-size: 1rem;
  line-height: 1.5; }


p, ul, ol, pre, table, blockquote {
  margin-top: 0rem;
  margin-bottom: 1.5rem; }

ul ul, ol ol, ul ol, ol ul {
  margin-top: 0rem;
  margin-bottom: 0rem; }


/* Let's make sure all's aligned */
hr, .hr {
  border: 1px solid;
  margin: -1px 0; }

a, b, i, strong, em, small, code {
  line-height: 0; }

sub, sup {
  line-height: 0;
  position: relative;
  vertical-align: baseline; }


